/**
 * @param {Object} dealer
 * @param {Object} player
 * @returns  {Number} : 
 */
function compareHands(dealer, player) {
  if (dealer.isFumble) {
    // 
    if (player.isFumble) {
      // : 
      return -1;
    }
    if (player.power < 0) {
      // : 
      return 1;
    }
    // 
    return player.power;
  }

  if (player.isFumble) {
    // 
    if (dealer.power > 0) {
      // 
      return dealer.power * -1;
    }

    // 
    return -1;
  }

  if (dealer.power >= 2 && dealer.power >= player.power && player.power > 0) {
    // 
    return Math.min(
      dealer.power * -1,
      player.power // 
    );
  }
  if (player.power >= 2 && player.power > dealer.power && dealer.power > 0) {
    // 
    // 
    return player.power;
  }

  if (player.power === 1 && dealer.power === 1) {
    // 
    if (Number(dealer.name) >= Number(player.name)) {
      // 
      return -1;
    }
    return 1;
  }

  // 
  if (dealer.power >= player.power) {
    return player.power; //
  }
  if (dealer.power < player.power && player.power < 0) {
    // 
    return dealer.power * -1; // 
  }

  // 
  return player.power; // === 1
}

export default compareHands;
